import Card from "./Card";
import Switch from "./Switch";

export default function NotificationCard(){
    return(
        <Card
            title=':'
        >
            <Switch
                title=""
                check={true}
            />
            <Switch
                title=""
                check={true}
            />
            <Switch
                title=""
                check={true}
            />
            <Switch
                title=" "
                check={true}
            />
            <Switch
                title="E-mail "
                check={true}
            />
        </Card>
)
}