import { useState } from "react";
import Card from "./Card";
import SettingsInput from "./SettingsInput";
import Switch from "./Switch";


export default function MailCard(){
    const [mail, setMail] = useState('')
    const [phone, setPhone] = useState('')
    return(
        <Card
            title="  :"
        >
            <Switch
                title=" "
                check={false}
            />
            <SettingsInput
                label='Email:'
                placeholder='B email'
                type='email'
                value={mail}
                onChange={setMail}
            />
            <SettingsInput
                label=':'
                placeholder='B '
                type='text'
                value={phone}
                onChange={setPhone}
            />
        </Card>
    )
}